@import "styles/vars";

.catalog-container {
   display: grid;
   grid-template-columns: repeat(4, 1fr);
   grid-template-rows: repeat(5, auto);

   @media (max-width: $media2 + px) {
      grid-template-rows: repeat(4, auto);
   }

   @media (max-width: $media6 + px) {
      grid-template-rows: repeat(5, auto);
      grid-template-columns: 1fr;
   }

   &__title {
      grid-area: 1 / 1 / 2 / 4;
      margin-bottom: 0.6em;

      font-weight: 500;
      font-size: 40px;
      line-height: 120%;
      text-transform: uppercase;
      color: #111111;

      @media (max-width: $media2 + px) {
         grid-area: 1 / 1 / 2 / 3;
         font-size: 30px;
      }

      @media (max-width: 768px) {
         font-size: 20px;
      }

      @media (max-width: $media6 + px) {
         grid-area: 1 / 1 / 2 / 2;
      }
   }

   &__sort-widget {
      margin-bottom: 25px;
      grid-area: 1 / 4 / 2 / 5;

      @media (max-width: $media2 + px) {
         grid-area: 1 / 3 / 2 / 5;
      }

      @media (max-width: $media6 + px) {
         grid-area: 3 / 1 / 4 / 2;
      }
   }
}

.sort-widget {
   display: flex;
   align-items: center;
   justify-content: flex-end;

   @media (max-width: $media6 + px) {
      justify-content: flex-start;
   }

   &__title {
      margin-right: 10px;
      font-weight: 500;
      font-size: 17px;
      line-height: 150%;
      color: #111111;
   }
}
